import React from 'react';
import DemoBlock from 'docs/mobileComponents/DemoBlock';
import Picker from '../Picker';
import InputItem from '../../input-item';
import data from './data';

const seasons = [
  [
    {
      label: '2013',
      value: '2013',
    },
    {
      label: '2014',
      value: '2014',
    },
  ],
  [
    {
      label: '',
      value: '',
    },
    {
      label: '',
      value: '',
    },
  ],
];

const district = [
  [
    {
      label: '2013',
      value: '2013',
    },
    {
      label: '2014',
      value: '2014',
    },
  ],
];

export default class Demo extends React.Component {
  state = {
    inputValue: [],
    inputValue2: [],
  }

  onScrollChange = (value) => {
    console.log('onScrollChange', value);
  }

  onOk = (value, labelValue) => {
    console.log('onOk', value);
    console.log('labelValue', labelValue);

    this.setState({
      inputValue: [value[0]],
    });
  }

  onDismiss = () => {
    console.log('onDismiss');
  }

  render() {
    return (
      <div>
        <DemoBlock title="">
          <Picker
            className="fortest"
            data={district}
            value={this.state.inputValue}
            cols={1}
            cascade={false}
            title=""
            disabled={this.state.disabled}
            onDismiss={this.onDismiss}
            onOk={this.onOk}
          >
            <InputItem
              label=""
              readOnly
              value={this.state.inputValue}
              placeholder=""
            />
          </Picker>
        </DemoBlock>
        <DemoBlock title="">
          <Picker
            className="fortest"
            data={seasons}
            cols={2}
            title=""
            cascade={false}
            disabled={this.state.disabled}
            onDismiss={this.onDismiss}
            value={this.state.inputValue2}
            onOk={(value) => { this.setState({ inputValue2: value }); }}
          >
            <InputItem
              label=""
              readOnly
              value={this.state.inputValue2.join('-')}
              placeholder=""
            />
          </Picker>
        </DemoBlock>
        <DemoBlock title="">
          <Picker
            className="fortest"
            data={data}
            title=""
            cascade
            disabled={this.state.disabled}
            onDismiss={this.onDismiss}
            value={this.state.inputValue3 || []}
            onOk={(value, valueLabel) => { this.setState({ inputValue3: value, valueLabel }); }}
          >
            <InputItem
              label=""
              readOnly
              value={this.state.valueLabel}
              placeholder=""
            />
          </Picker>
        </DemoBlock>
      </div>
    );
  }
}
