import React from 'react';
import DemoBlock from 'docs/mobileComponents/DemoBlock';
import Button from '../../Button';
import PopupNumberKeyboard from '../PopupNumberKeyboard';
import Toast from '../../toast';

export default class Demo extends React.PureComponent {
  state = {
    showKeyboard: false,
  }

  onKeyboardClick = (value) => {
    Toast.info(value, 1, null, false);
    if (value === 'confirm') {
      this.setState({ showKeyboard: false });
    }
  }

  render() {
    return (
      <div>
        <DemoBlock title="1" className="has-padding">
          <Button
            size="small"
            inline
            onClick={() =>
              this.setState({
                showKeyboard: !this.state.showKeyboard,
                showKeyboardTwo: false,
              })}
          >
            
          </Button>
          <PopupNumberKeyboard
            visible={this.state.showKeyboard}
            onKeyboardClick={this.onKeyboardClick}
          />

        </DemoBlock>
        <DemoBlock title="2" className="has-padding">
          <Button
            size="small"
            inline
            onClick={() =>
              this.setState({
                showKeyboardTwo: !this.state.showKeyboardTwo,
                showKeyboard: false,
              })}
          >
            
          </Button>
          <PopupNumberKeyboard
            visible={this.state.showKeyboardTwo}
            onKeyboardClick={this.onKeyboardClick}
            theme="custom"
          />
        </DemoBlock>
      </div>
    );
  }
}
