import React from 'react';
import DemoBlock from 'docs/mobileComponents/DemoBlock';
import Checkbox from '../Checkbox';
import CheckboxGroup from '../CheckboxGroup';
import Cell from '../../Cell';
import './index.less';

function onChange(e) {
  console.log('Checkbox checked:', (e.target.checked));
}

function groupChange(value) {
  console.log('groupChange checked:', value);
}

export default class Demo extends React.Component {
  state = {
    value: false,
  }
  render() {
    const optionsWithDisabled = [
      { label: 'Apple', value: 'Apple' },
      { label: 'Pear', value: 'Pear' },
      { label: 'Orange', value: 'Orange', disabled: true },
    ];
    return (
      <div className="demo-checkbox">
        <DemoBlock title="" className="has-padding">
          <Checkbox
            checked
            onChange={onChange}
          >
            ()
          </Checkbox>
          <br />
          <Checkbox
            defaultChecked
            onChange={onChange}
          >
            ()
          </Checkbox>
        </DemoBlock>
        <DemoBlock title="" className="has-padding">
          <Checkbox
            onChange={onChange}
            disabled
          >
            
          </Checkbox>
          <br />
          <Checkbox
            defaultChecked
            onChange={onChange}
            disabled
          >
            
          </Checkbox>
        </DemoBlock>

        <DemoBlock title="" className="has-padding">
          <Checkbox
            defaultChecked
            className="selected-green"
            onChange={onChange}
          >
            
          </Checkbox>
        </DemoBlock>

        <DemoBlock title="" className="has-padding">
          <CheckboxGroup
            options={optionsWithDisabled}
            defaultValue={['Apple']}
            onChange={groupChange}
          />
        </DemoBlock>

        <DemoBlock title="" className="has-padding">
          <Cell title="" onClick={() => { this.setState({ value: !this.state.value }); }} value={<Checkbox checked={this.state.value} />} />
        </DemoBlock>
      </div>
    );
  }
}
