/* eslint no-console:0 */

import React from 'react';
import DemoBlock from 'docs/mobileComponents/DemoBlock';
import Switch from '../index';
import Cell from '../../Cell';

export default class Demo extends React.Component {
  state = {
    checked: false,
    cellChecked: false,
  }
  render() {
    return (
      <div className="demo-radio">
        <DemoBlock title="" className="has-padding">
          <Switch
            defaultChecked
            checked={this.state.checked}
            onChange={() => {
              this.setState({
                checked: !this.state.checked,
              });
            }}
          />
        </DemoBlock>
        <DemoBlock title="color" className="has-padding">
          <Switch
            defaultChecked
            color="red"
            onChange={() => {}}
          />
        </DemoBlock>
        <DemoBlock title="" className="has-padding">
          <Switch checked disabled />
        </DemoBlock>
        <DemoBlock title="" className="has-padding">
          <Switch
            platform="android"
          />
        </DemoBlock>
        <DemoBlock title="loading" className="has-padding">
          <Switch
            loading
          />
        </DemoBlock>
        <DemoBlock title="cell">
          <Cell
            title=""
            onClick={() => { this.setState({ cellChecked: !this.state.cellChecked }); }}
            value={<Switch checked={this.state.cellChecked} />}
          />
        </DemoBlock>
      </div>
    );
  }
}
