import classnames from 'classnames';
import React from 'react';
import TouchFeedback from 'react-tap-feedback';

function noop() {}

function fixControlledValue(value) {
  if (typeof value === 'undefined' || value === null) {
    return '';
  }
  return value;
}

const regexAstralSymbols = /[\uD800-\uDBFF][\uDC00-\uDFFF]|\n/g;

function countSymbols(text) {
  return text.replace(regexAstralSymbols, '_').length;
}

export default class TextareaItem extends React.PureComponent {
  static defaultProps = {
    prefixCls: 'panda-textarea',
    autoHeight: false,
    readOnly: false,
    disabled: false,
    placeholder: '',
    clear: false,
    rows: 1,
    onChange: noop,
    onBlur: noop,
    onFocus: noop,
    onErrorClick: noop,
    error: false,
    labelNumber: 5,
  };

  constructor(props) {
    super(props);

    this.state = {
      focus: false,
      value: props.value || props.defaultValue || '',
    };
  }

  focus = () => {
    this.textareaRef.focus();
  }

  componentWillReceiveProps(nextProps) {
    if ('value' in nextProps) {
      this.setState({
        value: fixControlledValue(nextProps.value),
      });
    }
  }

  componentDidMount() {
    if (this.props.autoHeight) {
      this.reAlignHeight();
    }
  }

  componentDidUpdate() {
    if (this.props.autoHeight && this.state.focus) {
      this.reAlignHeight();
    }
  }

  reAlignHeight = () => {
    const textareaDom = this.textareaRef;
    textareaDom.style.height = '';
    textareaDom.style.height = `${textareaDom.scrollHeight}px`;
  }

  componentWillUnmount() {
    if (this.debounceTimeout) {
      clearTimeout(this.debounceTimeout);
      this.debounceTimeout = null;
    }
  }

  onChange = (e) => {
    const value = e.target.value;
    if ('value' in this.props) {
      this.setState({ value: this.props.value });
    } else {
      this.setState({ value });
    }

    this.props.onChange(value);
    //  defaultValue  componentDidUpdate 
    this.componentDidUpdate();
  }

  onBlur = (e) => {
    this.debounceTimeout = setTimeout(() => {
      if (document.activeElement !== this.textareaRef) {
        this.setState({
          focus: false,
        });
      }
    }, 100);
    const value = e.currentTarget.value;
    this.props.onBlur(value);
  }

  onFocus = (e) => {
    if (this.debounceTimeout) {
      clearTimeout(this.debounceTimeout);
      this.debounceTimeout = null;
    }
    this.setState({
      focus: true,
    });
    const value = e.currentTarget.value;
    this.props.onFocus(value);
  }

  onErrorClick = () => {
    this.props.onErrorClick();
  }

  clearInput = () => {
    this.setState({ value: '' });

    this.props.onChange('');
  }

  render() {
    const { prefixCls, readOnly, style, clear, children, error, className, count,
      labelNumber, label, autoHeight, defaultValue, ...otherProps } = this.props;
    const { disabled } = otherProps;
    const { value, focus } = this.state;
    const hasCount = count > 0 && this.props.rows > 1;

    const wrapCls = classnames(
      `${prefixCls}-wrapper`,
      {
        [`${prefixCls}--disabled`]: disabled,
        [`${prefixCls}-wrapper-single-line`]: this.props.rows === 1 && !autoHeight,
        [`${prefixCls}--error`]: error,
        [`${prefixCls}--focus`]: focus,
        [`${prefixCls}-has-count`]: hasCount,
      },
      className,
    );

    const labelCls = classnames(`${prefixCls}-label`);

    const characterLength = countSymbols(value);

    delete otherProps.onErrorClick;

    return (
      <div className={wrapCls}>
        {label && <div className={labelCls}>{label}</div>}
        <div className={`${prefixCls}-control`}>
          <textarea
            ref={el => (this.textareaRef = el)}
            {...otherProps}
            value={value}
            onChange={this.onChange}
            onBlur={this.onBlur}
            onFocus={this.onFocus}
            readOnly={readOnly}
            style={style}
          />
        </div>
        {
          clear &&
          !readOnly &&
          value &&
          characterLength > 0 && (
            <TouchFeedback activeClassName={`${prefixCls}-clear-active`}>
              <div
                className={`${prefixCls}-clear`}
                onClick={this.clearInput}
              />
            </TouchFeedback>
          )}
        {error && (
          <div
            className={`${prefixCls}-error-extra`}
            onClick={this.onErrorClick}
          />
        )}
        {hasCount && (
          <span className={`${prefixCls}-count`}>
            <span>{value ? characterLength : 0}</span>/{count}
          </span>
        )}
      </div>
    );
  }
}
