/* eslint no-console:0 */

import React from 'react';
import DemoBlock from 'docs/mobileComponents/DemoBlock';
import Radio from '../index';
import RadioGroup from '../RadioGroup';
import Cell from '../../Cell';
import './index.less';

function onChange(e) {
  console.log('Radio checked:', (e.target.checked));
}

function groupChange(e) {
  console.log('groupChange checked:', (e.target.value));
}

export default class Demo extends React.Component {
  state = {
    value: 1,
  }
  render() {
    const optionsWithDisabled = [
      { label: 'Apple', value: 'Apple' },
      { label: 'Pear', value: 'Pear' },
      { label: 'Orange', value: 'Orange', disabled: true },
    ];
    return (
      <div className="demo-radio">
        <DemoBlock title="" className="has-padding">
          <Radio
            onChange={onChange}
          >
            Radio
          </Radio>
        </DemoBlock>
        <DemoBlock title="" className="has-padding">
          <Radio
            onChange={onChange}
            disabled
          >
            
          </Radio>
          <br />
          <Radio
            defaultChecked
            onChange={onChange}
            disabled
          >
            
          </Radio>
        </DemoBlock>

        <DemoBlock title="" className="has-padding">
          <Radio
            defaultChecked
            className="selected-red"
            onChange={onChange}
          >
            
          </Radio>
        </DemoBlock>

        <DemoBlock title="" className="has-padding">
          <RadioGroup
            name="test"
            options={optionsWithDisabled}
            defaultValue="Pear"
            onChange={groupChange}
          />
        </DemoBlock>

        <DemoBlock title="">
          <Cell title="" onClick={() => { this.setState({ value: 1 }); }} value={<Radio checked={this.state.value === 1} />} />
          <Cell title="" onClick={() => { this.setState({ value: 2 }); }} value={<Radio checked={this.state.value === 2} />} />
        </DemoBlock>
      </div>
    );
  }
}
