import React from 'react';
import DemoBlock from 'docs/mobileComponents/DemoBlock';
import closest from 'src/utils/closest';
import Button from 'src/Button';
import Modal, { alert } from '../index';
import './index.less';

export default class Demo extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modal1: false,
      animationType: 'slide-up',
    };
  }
  showModal = (key, value) => (e) => {
    e.preventDefault(); //  Android 

    this.setState({
      [key]: true,
      animationType: value,
    });
  }
  showModal2 = () => (e) => {
    e.preventDefault(); //  Android 
    alert(
      { title: 'sdfsdf' },
      [
        { text: 'Ok', onPress: () => console.log('oksdfsdf') },
        { text: 'Cancel', onPress: () => console.log('oksdfsdf') },
      ],
    );
  }

  onClose = key => () => {
    this.setState({
      [key]: false,
    });
  }

  onWrapTouchStart = (e) => {
    // fix touch to scroll background page on iOS
    if (!/iPhone|iPod|iPad/i.test(navigator.userAgent)) {
      return;
    }
    const pNode = closest(e.target, '.am-modal-content');
    if (!pNode) {
      e.preventDefault();
    }
  }

  render() {
    return (
      <div>
        <DemoBlock title="" className="modal-demo">
          <Button inline size="small" onClick={this.showModal('modal1')}></Button>
          <Modal
            visible={this.state.modal1}
            maskClosable={false}
            onClose={this.onClose('modal1')}
            title="Title"
            footer={[{ text: '', onPress: () => { console.log('ok'); this.onClose('modal1')(); } }]}
            wrapProps={{ onTouchStart: this.onWrapTouchStart }}
          >
            <div>
              <p></p>
              <p></p>
              <p></p>
              <p></p>
            </div>
          </Modal>
        </DemoBlock>
        <DemoBlock title="popup" className="modal-demo">
          <Button inline size="small" onClick={this.showModal('modal2', 'slide-up')}></Button>
          <Button inline size="small" onClick={this.showModal('modal2', 'slide-down')}></Button>
          <Modal
            popup
            animationType={this.state.animationType}
            visible={this.state.modal2}
            transparent
            maskClosable={false}
            onClose={this.onClose('modal2')}
            title="Title"
            footer={[{ text: '', onPress: () => { console.log('ok'); this.onClose('modal2')(); } }]}
            wrapProps={{ onTouchStart: this.onWrapTouchStart }}
          >
            <div>
              <p></p>
              <p></p>
              <p></p>
              <p></p>
            </div>
          </Modal>
        </DemoBlock>
        <DemoBlock title="alert" className="modal-demo">
          <Button
            inline
            size="small"
            onClick={() => alert(
              { title: '' },
              [
                { text: '', onPress: () => console.log('oksdfsdf') },
                { text: '', onPress: () => console.log('oksdfsdf') },
              ],
            )}
          >
            (alert)
          </Button>
        </DemoBlock>
      </div>
    );
  }
}

